"use client"

import { useState, useEffect, useTransition } from "react"
import { useRouter } from "next/navigation"
import { Header } from "./finance/header"
import { SummaryCards } from "./finance/summary-cards"
import { StatisticsChart } from "./finance/statistics-chart"
import { TransactionTabs } from "./finance/transaction-tabs"
import type { Transaction, TransactionType } from "@/lib/finance-data"
import { months } from "@/lib/finance-data"
import {
  getTransactions,
  createTransaction,
  updateTransaction,
  deleteTransaction,
  getTransactionStats,
} from "@/app/actions/transactions"
import { convertDateToISO, convertDateFromISO } from "@/lib/api-utils"
import { toast } from "sonner"

interface FinanceTrackerProps {
  initialTransactions: Transaction[]
  initialTotals: { income: number; expense: number; savings: number }
  initialMonth: string
}

export default function FinanceTracker({
  initialTransactions,
  initialTotals,
  initialMonth,
}: FinanceTrackerProps) {
  const router = useRouter()
  const [isPending, startTransition] = useTransition()
  const [selectedMonth, setSelectedMonth] = useState(initialMonth)
  const [transactions, setTransactions] = useState<Transaction[]>(initialTransactions)
  const [totals, setTotals] = useState(initialTotals)

  // Fetch transactions when month changes
  useEffect(() => {
    startTransition(async () => {
      const result = await getTransactions({ month: selectedMonth })
      if (result.success && result.data) {
        // Convert dates from ISO to dd/MM/yyyy format for display
        const formattedTransactions = result.data.map((t) => ({
          ...t,
          date: convertDateFromISO(t.date),
        }))
        setTransactions(formattedTransactions)

        // Fetch and update stats
        const statsResult = await getTransactionStats(selectedMonth)
        if (statsResult.success && statsResult.data) {
          setTotals({
            income: statsResult.data.totals.income,
            expense: statsResult.data.totals.expense,
            savings: statsResult.data.totals.savings,
          })
        }
      }
    })
  }, [selectedMonth])

  const handleAddTransaction = async (transaction: Omit<Transaction, "id">) => {
    startTransition(async () => {
      // Convert date from dd/MM/yyyy to YYYY-MM-DD
      const isoDate = convertDateToISO(transaction.date)

      const result = await createTransaction({
        title: transaction.title,
        amount: transaction.amount,
        date: isoDate,
        category: transaction.category,
        type: transaction.type,
      })

      if (result.success && result.data) {
        // Convert date back to dd/MM/yyyy for display
        const formattedTransaction = {
          ...result.data,
          date: convertDateFromISO(result.data.date),
        }
        setTransactions([formattedTransaction, ...transactions])
        
        // Refresh stats
        const statsResult = await getTransactionStats(selectedMonth)
        if (statsResult.success && statsResult.data) {
          setTotals({
            income: statsResult.data.totals.income,
            expense: statsResult.data.totals.expense,
            savings: statsResult.data.totals.savings,
          })
        }
        
        toast.success("লেনদেন যোগ করা হয়েছে")
        router.refresh()
      } else {
        toast.error(result.error || "লেনদেন যোগ করতে ব্যর্থ হয়েছে")
      }
    })
  }

  const handleDeleteTransaction = async (id: string) => {
    startTransition(async () => {
      const result = await deleteTransaction(id)

      if (result.success) {
        setTransactions(transactions.filter((t) => t.id !== id))
        
        // Refresh stats
        const statsResult = await getTransactionStats(selectedMonth)
        if (statsResult.success && statsResult.data) {
          setTotals({
            income: statsResult.data.totals.income,
            expense: statsResult.data.totals.expense,
            savings: statsResult.data.totals.savings,
          })
        }
        
        toast.success("লেনদেন মুছে ফেলা হয়েছে")
        router.refresh()
      } else {
        toast.error(result.error || "লেনদেন মুছতে ব্যর্থ হয়েছে")
      }
    })
  }

  const handleEditTransaction = async (id: string, updatedTransaction: Omit<Transaction, "id">) => {
    startTransition(async () => {
      // Convert date from dd/MM/yyyy to YYYY-MM-DD
      const isoDate = convertDateToISO(updatedTransaction.date)

      const result = await updateTransaction(id, {
        title: updatedTransaction.title,
        amount: updatedTransaction.amount,
        date: isoDate,
        category: updatedTransaction.category,
        type: updatedTransaction.type,
      })

      if (result.success && result.data) {
        // Convert date back to dd/MM/yyyy for display
        const formattedTransaction = {
          ...result.data,
          date: convertDateFromISO(result.data.date),
        }
        setTransactions(transactions.map((t) => (t.id === id ? formattedTransaction : t)))
        
        // Refresh stats
        const statsResult = await getTransactionStats(selectedMonth)
        if (statsResult.success && statsResult.data) {
          setTotals({
            income: statsResult.data.totals.income,
            expense: statsResult.data.totals.expense,
            savings: statsResult.data.totals.savings,
          })
        }
        
        toast.success("লেনদেন আপডেট করা হয়েছে")
        router.refresh()
      } else {
        toast.error(result.error || "লেনদেন আপডেট করতে ব্যর্থ হয়েছে")
      }
    })
  }

  const getFilteredTransactions = (type: TransactionType) => {
    return transactions.filter((t) => t.type === type && t.month === selectedMonth)
  }

  return (
    <div className="container mx-auto px-4 py-6 max-w-7xl">
      <Header selectedMonth={selectedMonth} onMonthChange={setSelectedMonth} months={months} />

      <SummaryCards totals={totals} />

      <StatisticsChart totals={totals} />

      <TransactionTabs
        incomeTransactions={getFilteredTransactions("income")}
        expenseTransactions={getFilteredTransactions("expense")}
        savingsTransactions={getFilteredTransactions("savings")}
        onAdd={handleAddTransaction}
        onDelete={handleDeleteTransaction}
        onEdit={handleEditTransaction}
        selectedMonth={selectedMonth}
      />
    </div>
  )
}
